import ContentstackUIExtension from '@contentstack/ui-extensions-sdk';
import Vue from 'vue';
import Vuex from 'vuex';

Vue.use(Vuex);

const store = new Vuex.Store({
  state: {
    selectedImageUrl: null,
    focalPoint: {
      x: 0.5,
      y: 0.5,
    },
    searchResults: [],
    searchText: '',
  },
  getters: {
  },
  mutations: {
    setFocalPoint(state, payload) {
      state.focalPoint.x = payload.x;
      state.focalPoint.y = payload.y;
    },
    setSelectedImage(state, payload) {
      state.selectedImageUrl = payload.url;
    },
    setSearchText(state, payload) {
      state.searchText = payload.searchText;
    },
    setSearchResults(state, payload) {
      state.searchResults = payload.results;
    },
  },
  actions: {
    searchForAssets({ commit }, payload) {
      console.log('searching');
      console.log(payload.searchText);
      ContentstackUIExtension.init()
        .then((extension) => {
          let assetQuery = extension.stack.Asset.Query();
          if (payload.searchText) {
            assetQuery = assetQuery.search(payload.searchText);
          }
          return assetQuery.limit(10).find();
        })
        .then((results) => {
          console.log(results.assets);
          commit({
            type: 'setSearchResults',
            results: results.assets,
          });
        });
    },
  },
});

export default store;
